import * as React from "react";

import { cn } from "../../utils/cn";

type CustomProps = {
  slot1?: string;
};

export interface DefaultSlotProps extends React.HTMLProps<HTMLDivElement>, CustomProps {}

const DefaultSlot = React.forwardRef<HTMLDivElement, DefaultSlotProps>(
  ({ className, children, slot1, ...props }, ref) => {
    return (
      <div className={cn("", className)} ref={ref} {...props}>
        {children}
        {slot1}
      </div>
    );
  },
);

DefaultSlot.displayName = "DefaultSlot";

export { DefaultSlot };
