import * as React from "react";

import { cn } from "../../utils/cn";

export interface TaglineProps extends React.HTMLProps<HTMLParagraphElement> {}

const Tagline = React.forwardRef<HTMLParagraphElement, TaglineProps>(
  ({ className, ...props }, ref) => {
    return <p className={cn("font-semibold md:text-md", className)} ref={ref} {...props} />;
  },
);
Tagline.displayName = "Tagline";

export { Tagline };
