import * as React from "react";

import { cn } from "../../utils/cn";

type CustomProps = {
  slot1?: string;
};

export interface DefaultSectionProps extends React.HTMLProps<HTMLDivElement>, CustomProps {}

const DefaultSection = React.forwardRef<HTMLDivElement, DefaultSectionProps>(
  ({ className, children, slot1, ...props }, ref) => {
    return (
      <section className={cn("px-[5%] py-16 md:py-24 lg:py-28", className)} ref={ref} {...props}>
        <div className="container">
          <div className="flex flex-col gap-x-20 gap-y-12 md:gap-y-16 lg:justify-center">
            {children}
            {slot1}
          </div>
        </div>
      </section>
    );
  },
);

DefaultSection.displayName = "DefaultSection";

export { DefaultSection };
