import * as React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "../../utils/cn";

const variants = cva("", {
  variants: {
    textSize: {
      large: "text-md md:text-lg",
      medium: "text-base md-text:md",
      regular: "text-base md:text-md",
      small: "text-sm",
      tiny: "text-xs",
    },
  },
  defaultVariants: {
    textSize: "regular",
  },
});

type CustomProps = {
  text?: string;
};

export interface TextProps
  extends React.HTMLProps<HTMLParagraphElement>,
    CustomProps,
    VariantProps<typeof variants> {}

const Text = React.forwardRef<HTMLHeadingElement, TextProps>(
  ({ className, children, text, textSize, ...props }, ref) => {
    return (
      <p className={cn(variants({ textSize, className }))} ref={ref} {...props}>
        {text}
        {children}
      </p>
    );
  },
);
Text.displayName = "Text";

export { Text };
