import * as React from "react";
import { cva, type VariantProps } from "class-variance-authority";
import { cn } from "../../utils/cn";

const headingVariants = cva("text-black", {
  variants: {
    headingSize: {
      h1: "mb-5 text-6xl font-bold md:mb-6 md:text-9xl lg:text-10xl",
      h2: "mb-5 text-5xl font-bold md:mb-6 md:text-7xl lg:text-8xl",
      h3: "mb-5 text-4xl font-bold leading-[1.2] md:mb-6 md:text-5xl lg:text-6xl",
      h4: "mb-3 text-2xl font-bold md:mb-4 md:text-3xl md:leading-[1.3] lg:text-4xl",
      h5: "mb-3 text-xl font-bold md:text-2xl",
      h6: "mb-3 text-md font-bold leading-[1.4] md:mb-4 md:text-xl",
    },
  },
  defaultVariants: {
    headingSize: "h2",
  },
});

type CustomProps = {
  text?: string;
};

export interface HeadingProps
  extends React.HTMLProps<HTMLHeadingElement>,
    CustomProps,
    VariantProps<typeof headingVariants> {}

const Heading = React.forwardRef<HTMLHeadingElement, HeadingProps>(
  ({ className, children, text, headingSize, ...props }, ref) => {
    const Comp = headingSize ? headingSize : "h2";
    return (
      <Comp className={cn(headingVariants({ headingSize, className }))} ref={ref} {...props}>
        {text}
        {children}
      </Comp>
    );
  },
);
Heading.displayName = "Heading";

export { Heading };
